<template>
	<div id="container">

		
		<div class="top">
			<el-button type="primary" @click="addObj.flag=true">添加游戏</el-button>
		</div>
		<el-table 
			stripe
			:data="tableData.filter(data => !search || String(data.phone).toLowerCase().includes(search.toLowerCase()))" 
			style="width: 100%"
			max-height='730px'
			v-loading="loading"
			>
			<el-table-column prop="name" label="游戏名字"> </el-table-column>
			<el-table-column prop="money" label="游戏价格"> </el-table-column>
			<el-table-column fixed="right" label="操作">
			  <template slot-scope="scope">
				<el-button size="mini" @click="handleEdit(scope.$index, scope.row);">编辑</el-button>
				<el-button size="mini" @click.native.prevent="handleDelete(scope.$index, scope.row)">移除</el-button>
			  </template>
			</el-table-column>
		</el-table>

		<!-- 添加 -->
		<el-dialog title="添加游戏" :visible.sync="addObj.flag" width="50%" center>
			<el-form label-position="left" label-width="100px">
				<el-form-item label="游戏名字:">
					<el-input v-model="addObj.ename" type="text" size="small"></el-input>
				</el-form-item>
				<el-form-item label="价格:">
					<el-input v-model="addObj.money" type="number" size="small"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="addclear()">取 消</el-button>
				<el-button type="primary" @click="addTrue()">确 定</el-button>
			</div>
		</el-dialog>

		<!-- 编辑 -->
		<el-dialog title="编辑信息" :visible.sync="changeObj.flag" width="50%" center>
			<el-form label-position="left" label-width="100px">
				<el-form-item label="游戏名字:">
					<el-input v-model="changeObj.ename" type="text" size="small"></el-input>
				</el-form-item>
				<el-form-item label="价格:">
					<el-input v-model="changeObj.money" type="number" size="small"></el-input>
				</el-form-item>
		  	</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="changeObj.flag=false">取 消</el-button>
				<el-button type="primary" @click="changeTrue">确 定</el-button>
			</div>
		</el-dialog>

	<el-pagination
		v-if="tableData"
		background
		layout="sizes, prev, pager, next, jumper"
		:total="fenye.total"
		:page-sizes="[10, 20, 30, 50]"
		@current-change="yebian"
		@size-change='tiaobian'
		:page-size="fenye.page_size"
		:current-page="fenye.current_page">
	</el-pagination>
	</div>
</template>

<script>

 export default {
	 //将每组需要操作的数据，分成每个对象，在方法里进行再次的处理
	data() {
		return {
			//公用部分
			tableData: [{name:'sad'}],//列表的数据
			space:'',//地址的数据（从vuex里获得）
			userList:[],//用户的列表信息
			loading:true,
			
			search:'',//搜索用
			top_sou:'',
			addObj:{//添加用
				ename:'',
				money:'',
				flag:false
			},
			fenye:{//分页用
				total:1,//总条数
				page_size:10,//单页条数
				current_page:1,//页码
			},
			changeObj:{//编辑用
				flag:false,
				ename:'',
				money:''
			}
		}
	},
	methods:{
		//编辑按钮
		handleEdit(index, row) {
			this.changeObj.flag = true
			this.changeObj.ename = this.tableData[index].ename
			this.changeObj.money = this.tableData[index].money
			this.changeObj.id = this.tableData[index].id
		},
		//编辑确定按钮
		changeTrue(){
			this.loading = true
			let that = this
			let obj = {
				token:this.$store.state.userInfo.token,
				name:this.$store.state.userInfo.name,
				id:this.changeObj.id,
				ename:this.changeObj.ename,
				money:this.changeObj.money
			}
			this.$axios.post("/gamelistEdit",obj).then(function(res){
				if(res.data.code == '1'){
					that.$message({
						type: 'info',
						message: res.data.message,
					}); 
				}else{
					that.changeObj.flag=false
					that.xinxiList(that.fenye.current_page,that.fenye.page_size)
					that.$message({
						type: 'success',
						message: res.data.message
					});
				}
			})
		},
		//删除按钮
		handleDelete(index, row) {
			this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				this.tableData.splice(index,1)
				this.$message({
					type: 'success',
					message: '删除成功!'
				});
			}).catch(() => {
				this.$message({
					type: 'info',
					message: '已取消删除'
				});          
			});
		},
		//分页
		yebian(currentPage){//页面的页码发生变化的时候
			this.loading = true
			this.fenye.current_page = currentPage
			this.xinxiList(currentPage,this.fenye.page_size)
		},
		tiaobian(ind){//页面的条数发生变化的时候
		    this.loading = true
			this.fenye.page_size = ind
			this.xinxiList(this.fenye.current_page,ind)
		},
		//添加游戏的确定按钮
		addTrue(){
			this.loading = true
			//下面是处理数据，传给后台
			let that = this
			let obj = {
				'name':this.$store.state.userInfo.name,
				'token':this.$store.state.userInfo.token,
				'ename':this.addObj.ename,
				'money':this.addObj.money
			}
			console.log(obj)
			//提交申请
			this.$axios.post("/gamelistAdd",obj).then(function(res){
				//创建失败
				if(res.data.code == '1'){
					that.$message({
						type: 'info',
						message: res.data.message,
					}); 
				}else{
				//创建成功
					that.$message({
						type: 'success',
						message: res.data.message
					});
					that.xinxiList(that.fenye.current_page,that.fenye.page_size)
				}
				that.addclear()
			})
		},
		//在添加用户完成或取消，消除内容
		addclear(){
			this.addObj.ename = ''
			this.addObj.money = ''
			this.addObj.flag = false
		},
		//门店设备列表
		xinxiList(ind,num){
			let that = this
			let name = this.$store.state.userInfo.name
			let token = this.$store.state.userInfo.token
			let obj = {
				'name':this.$store.state.userInfo.name,
				'token':this.$store.state.userInfo.token,
				'page':ind,
				'pageSize':num
			}
			this.$axios.post("/gamelist",obj).then(function(res){
				that.fenye.total = res.data.pageCount * res.data.pageSize
				that.tableData = res.data.data
				that.loading = false
				console.log(that.tableData)
			})
		}
		
	},
	//挂载完成执行的生命钩子函数
	created () {
		//获得三级菜单，处理数据
		this.space = this.$store.state.space
		//门店设备列表的数据
		this.xinxiList(this.fenye.current_page,this.fenye.page_size)
		//获得门店主列表信息
		let that = this
		let obj = {
			'name':this.$store.state.userInfo.name,
			'token':this.$store.state.userInfo.token
		}
		this.$axios.post("/user",obj).then(function(res){
			res.data.data.forEach(function(val,ind){
				that.userList.push({
					'label':val.name,
					'value':val.id
				})
			})
		})
	}
}
</script>



<style scoped lang="less">


#container{
  padding-right: 10px;
}
.top{
  width:100%;
  height: 60px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  &>.el-input{
	  width: 40% !important;
  }
}
.el-table-column label{
	text-align: center;
}
</style>
